import orderBtn from "@/components/order-button.vue";
import Url from 'url-parse';
export default {
	components: {
		orderBtn,
	},
	data() {
		return {
			payType: 1, //1微信 2支付宝
			orderData: {},
			webviewUrl: 'about:blank',
		}
	},
	onLoad(options) {
		if (options.q) {
		 options.q = decodeURIComponent(options.q);
		 const qrcodeUrl = new Url(options.q);
		 qrcodeUrl.set('query', {
		 redirect_url: 'orderParticulars', 
		 });
		 this.webviewUrl = qrcodeUrl.toString();
		 }
	},
	onShow(){
	},
	methods: {
		returnDate(e) {
			if (e.success) {
				this.$api.msg(e.msg)
				this.loadData(id)
			} else {
				this.$api.msg(e.msg)
				this.loadData(id)
			}
		},
		loadData(id) {
			this.$api.payment.getOrder(id).then(data => {
				this.orderData = data
				this.orderData.discount = 0
				this.orderData.order_goods.forEach(item => {
					this.orderData.discount += (item.fake_price - item.price)
				})
			})
		},
		//显示优惠券面板
		toggleMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(() => {
				this.maskState = state;
			}, timer)
		},
